<template>
  <main class="content container" v-if="pageLoadFailed">Не удалось загрузить товар</main>
  <main class="content container" v-else>
    <BaseBreadcrumbs :links="breadcrumbLinks" />

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img v-if="product.image" width="570" height="570" :src="product.image" :alt="product.title">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ renderValue(['product', 'id']) }}</span>
        <h2 class="item__title">
          {{ renderValue(['product', 'title']) }}
        </h2>
        <div class="item__form">
          <form class="form" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ numberHelper.price(renderValue(['product', 'price'], 0)) }} ₽
            </b>

            <fieldset class="form__block" v-if="product.colors">
              <legend class="form__legend">Цвет:</legend>

              <ProductColors
                v-model="color"
                :colors="product.colors"
              />
            </fieldset>

            <fieldset class="form__block" v-if="product.offers && !isColorPropMain">
              <legend class="form__legend">Цвет:</legend>

              <ProductOffers
                v-model="prop"
                :props="product.offers"
                class="sizes--primery"
              />
            </fieldset>

            <div class="item__row">
              <ProductAmount v-model="amount" :svg-size="12" />

              <button class="button button--primery" type="submit" :disabled="productAddingToCart">
                В корзину
              </button>
            </div>

            <div v-show="productAddedToCart">Товар добавлен в корзину</div>
            <div v-show="productAddingToCart">Товар добавляется в корзину</div>
          </form>
        </div>
      </div>

      <div v-if="pageLoading">Идёт загрузка характеристик товара...</div>
      <div class="item__desc" v-else-if="!pageLoading && product.specifications.length > 0">
        <ul class="tabs">
          <li class="tabs__item">
            <span class="tabs__link tabs__link--current">Характеристики</span>
          </li>
        </ul>

        <div class="item__content">
          <div v-for="spec in product.specifications" :key="spec.id">
            <h3>{{ spec.title }}</h3>
            <p>{{ spec.value }}</p>
          </div>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import PageLoading from '@/mixins/page-loading.mixin';
import ProductColors from '@/components/products/ProductColors';
import ProductAmount from '@/components/products/ProductAmount';
import { mapMutations, mapState, mapActions } from 'vuex';
import BaseBreadcrumbs from '@/components/BaseBreadcrumbs';
import ProductOffers from '@/components/products/ProductOffers';
import ProductColorMainPropCheck from '@/mixins/product-color-main-prop-check.mixin';

export default {
  components: {
    ProductOffers, BaseBreadcrumbs, ProductAmount, ProductColors,
  },
  mixins: [PageLoading, ProductColorMainPropCheck],
  data() {
    return {
      color: null,
      prop: null,
      amount: 1,
      productData: null,

      productAddedToCart: false,
      productAddingToCart: false,
    };
  },
  computed: {
    ...mapState('pageLoading', ['pageLoading', 'pageLoadFailed']),

    product() {
      return {
        id: 0,
        price: 0,
        title: 'загрузка...',

        ...this.productData,
      };
    },
    category() {
      return {
        id: 0,
        title: 'загрузка...',

        ...(this.productData ? this.productData.category : null),
      };
    },
    computed: {
      isColorPropMain() {
        return this.colorHelper.isColorProp(this.product.mainProp.id);
      },
    },
    breadcrumbLinks() {
      return [
        { title: 'Каталог', to: { name: 'main' } },
        { title: this.renderValue(['category', 'title']), to: { name: 'main' } },
        { title: this.renderValue(['product', 'title']) },
      ];
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        if (+this.$route.params.id > 0) {
          this.loadProduct(+this.$route.params.id);
        }
      },
      immediate: true,
    },
  },
  methods: {
    ...mapActions('cart', ['addProductToCart']),
    ...mapActions('product', ['loadProductData']),
    ...mapMutations('pageLoading', ['pageLoadStart', 'pageLoadStop', 'pageLoadFail']),

    async addToCart() {
      this.productAddedToCart = false;
      this.productAddingToCart = true;

      await this.addProductToCart({
        propId: this.prop,
        colorId: this.color,
        amount: this.amount,
      });

      this.productAddedToCart = true;
      this.productAddingToCart = false;
    },
    async loadProduct(productId) {
      this.pageLoadStart();
      this.pageLoadFail(false);

      try {
        this.productData = await this.loadProductData(productId);
      } catch (e) {
        console.log(e);
        this.pageLoadFail(true);
      } finally {
        this.pageLoadStop();
      }
    },
  },
};
</script>
